import React, {useState, useEffect} from 'react';
// Framework
import {Container, Row, Col} from 'react-bootstrap';
import axios from 'axios';
// Components
import Dices from '../components/Dices';
import TablePoints from '../components/TablePoints';
import TopBar from '../components/TopBar';
import BottomBar from '../components/BottomBar';
// Utils
import initialPoints from '../utils/initialPoints';

export default function Game() {
    let [rolled, setRolled] = useState<number[]>([]);
  let [side, setSide] = useState<number[]>([]);
  let [computedList, setComputed] = useState<any[]>([])
  let [round, setRound] = useState(1);
  let [turn, setTurn] = useState(0);
  let [points, setPoints] = useState({...initialPoints})
  let [equalTotal, setEqualTotal] = useState(0);
  let [upperTotal, setUpperTotal] = useState(0);
  let [lowerTotal, setLowerTotal] = useState(0);
  let [total, setTotal] = useState(0);

  // Reset game when page loaded

  useEffect(() =>reset(),[]);


  useEffect(() => {
    if (round >= 14) {
      alert("Game finished! points: " + total)
      reset();
    }
  },[round,total]);

  useEffect(() => {
     axios.post('/compute', {
      hand: rolled
    }).then((response) => computedListHandler(response.data))
    // FIXME:
    // eslint-disable-next-line react-hooks/exhaustive-deps
  },[rolled])

// Utils Functions
  let toSide =(dice: number) => {
    // let x = [...rolled];
    let y = [...side];
    // x.splice(x.indexOf(dice),1);
    y.push(dice);
    // setRolled(x);
    setSide(y);
  }

  let toRolled =(dice: number) => {
    // let x = [...rolled];
    let y = [...side];
    y.splice(y.indexOf(dice),1);
    // x.push(dice);
    // setRolled(x);
    setSide(y);
  }

  let computedListHandler = (arr: any[]) => {
    let tmpComputedList: any[] = [];
    arr.map((element) => {
      // Se c' la possibilit di segnare il punto aggiungi l'elemento in lista
      if ((points as any)[element.name] === "") {
        tmpComputedList.push(element)
      }
      return element;
    })
    // Se c' la possibilit di segnare e non  presente una opzione per segnare il punto aggiungi l'elemento in lista con punti 0
    for (let element in (points as any)) {
      if ((points as any)[element] === "" && tmpComputedList.find(o => o.name === element) === undefined && element !== "bonus" && element !== "yahtzee_bonus") {
        tmpComputedList.push({name: element, points: 0})
      }
    }
    setComputed(tmpComputedList);
    let et = Number(points.equal_1)+Number(points.equal_2)+Number(points.equal_3)+Number(points.equal_4)+Number(points.equal_5)+Number(points.equal_6)
    if (et >= 63) {
        (points as any)["bonus"] = 35
      }
    let ut = Number(et)+Number(points.bonus)
    let lt = Number(points.tris)+Number(points.poker)+Number(points.full)+Number(points.little_straight)+Number(points.big_straight)+Number(points.yahtzee)+Number(points.chance)+Number(points.yahtzee_bonus)
    let t = lt+ut
    setEqualTotal(et);
    setUpperTotal(ut);
    setLowerTotal(lt);
    setTotal(t);
  }

  // Post
 function getThrow() {
    axios.post('/throw', {
      "side": side
    }).then((response) => {
      let {hand} = response.data;
      setRolled(hand);
      setSide([]);
      setTurn(turn+1);
    })

  }
  function postPoints(name: string, point: number) {
    if ((points as any)[name] === "") {
      (points as any)[name] = point;
    } else if (name === "yahtzee" && (points as any)[name] !== "") {
      (points as any)["yahtzee_bonus"] += 100;
    }
    setRolled([])
    setSide([]);
    setRound(round+1);
    setTurn(0);
  }
  function reset() {
      setPoints({...initialPoints})
      setTurn(0);
      setRound(1);
      setRolled([]);
      setSide([]);
    }


  return (<>
          <TopBar 
            total={total}
            round={round}
          />
          <BottomBar 
            turn={turn}
            getThrow={getThrow}
          />
          <Container className='mt-md-5'>
            <Row>
              <Col md={6}>
                <TablePoints 
                  computedList={computedList} 
                  points={points}
                  equalTotal={equalTotal}
                  upperTotal={upperTotal}
                  lowerTotal={lowerTotal}
                  total={total}
                  postPoints={postPoints}
                />
              </Col>
              <Col md={6} className="p-0 m-0 mt-5 d-flex align-items-center">
                {
                  (rolled.length > 0) ? <Dices dices={rolled} turn={turn} toSide={toSide} toRolled={toRolled}/> : "Throw dices..."
                }
              </Col>
            </Row>
            
          </Container>
        </>

  );
}