import {Container} from "react-bootstrap";
import { Link } from "react-router-dom";

export default function Home() {
    return(<Container className="py-5 justify-content-center">
        <h1 className="display-1 text-center my-5">Yatzy</h1>
        <p className="text-muted text-center my-5">Play yatzy with this simulator, you can play with a friend for the player that score more points</p>
         <div className={`d-grid gap-2 d-md-flex ${(window.innerWidth > 768)? "justify-content-center" : ""}`}>
            <Link className="btn btn-primary btn-lg" to="/play">
                Play
            </Link>
            <Link className="btn btn-secondary btn-lg" to="rules">
                Rules
            </Link>
        </div>
        <footer className="fixed-bottom text-center text-muted">
            <p>Made with  by <a className="text-decoration-none" href="https://alienpingu.github.io/" target="_blank" rel="noopener noreferrer">alienpingu</a></p>
        </footer>
    </Container>);
}