import {Button ,Navbar} from 'react-bootstrap';
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
    throwBtn: {
        backgroundColor: "#2CB32C",
        fontWeight: "bold",
        textTransform: "uppercase",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center"
    },
    throwIndicator: {
        color: "white",
        backgroundColor: "#C01212",
        border: "2px solid #E03737",
        padding: "2px 12px",
        borderRadius: "50%",
        marginLeft: "1rem"
    }
}
export default function BottomBar(props: any) {
    let {classes} = jss.createStyleSheet(styles).attach()
    return (<Navbar fixed="bottom">
                <div className="w-100 d-grid gap-2 px-2">
                    <Button 
                        variant="success"
                        size="lg" 
                        className={`${classes.throwBtn} d-md-none`}
                        onClick={() =>props.getThrow()}
                        disabled={(props.turn >= 3)}
                    >
                       Throw <span className={classes.throwIndicator}>{3-props.turn}</span>
                    </Button>
                </div>
                <Button 
                        variant="success"
                        size="lg" 
                        className={`${classes.throwBtn} d-none d-md-flex m-5`}
                        onClick={() =>props.getThrow()}
                        disabled={(props.turn >= 3)}
                    >
                       Throw <span className={`${classes.throwIndicator}`}>{3-props.turn}</span>
                    </Button>
            </Navbar>)
}