import Container from "react-bootstrap/Container";
import { Link } from "react-router-dom";

export default function Rules() {
    return(<Container className="py-5">
        <Link to="/" className="text-link">Back to home</Link>
        <h1 className="fw-bold my-2">Rules</h1>
        <p>The game consists of a number of rounds. In each round, a player gets three rolls of the dice, although they can choose to end their turn after one or two rolls. After the first roll the player can save any dice they want and re-roll the other dice. This procedure is repeated after the second roll. The player has complete choice as to which dice to roll. It is possible to re-roll both dice that were or were not rolled before.</p>
        <p>The Yahtzee scorecard contains 13 different category boxes and in each round, after the third roll, the player must choose one of these categories. The score entered in the box depends on how well the five dice match the scoring rule for the category. Details of the scoring rules for each category are given below. As an example, one of the categories is called Three of a Kind. The scoring rule for this category means that a player only scores if at least three of the five dice are the same value. The game is completed after 13 rounds by each player, with each of the 13 boxes filled. The total score is calculated by summing all thirteen boxes, together with any bonuses.</p>
        <p>The Yahtzee scorecard contains 13 scoring boxes divided into upper and lower sections.</p>
        <h2 className="fw-bold my-2">Upper section</h2>
        <p>In the upper section there are six boxes. The score in each of these boxes is determined by adding the total number of dice matching that box.</p>
        <ul>
            <li>
                <span className="fw-bold">Aces </span>
                The sum of dice with the number 1
            </li>
            <li>
                <span className="fw-bold">Twos </span>
                The sum of dice with the number 2
            </li>
            <li>
                <span className="fw-bold">Threes </span>
                The sum of dice with the number 3
            </li>
            <li>
                <span className="fw-bold">Fours </span>
                The sum of dice with the number 4
            </li>
            <li>
                <span className="fw-bold">Fives </span>
                The sum of dice with the number 5
            </li>
            <li>
                <span className="fw-bold">Sixes </span>
                The sum of dice with the number 6
            </li>
        </ul>
        <p>If a player scores a total of 63 or more points in these six boxes, a bonus of 35 is added to the upper section score. Although 63 points corresponds to scoring exactly three-of-a-kind for each of the six boxes, a common way to get the bonus is by scoring four-of-a-kind for some numbers so that fewer of other numbers are needed. A player can earn the bonus even if they score a "0" in an upper section box.</p>
        <p>In order to gauge how well a player is doing in the upper section, they often refer to being "up" or "down" compared to the average of three required for each box. So that if a player scores four "sixes" they will be "6 up"; while if they then score just two "twos" they will then be only "4 up". Similarly, if a player starts with two "twos" they will be "2 down".</p>
        <h2 className="fw-bold my-2">Lower section</h2>
        <p>The lower section contains a number of poker-themed categories with specific point values:</p>
         <ul>
            <li>
                <span className="fw-bold">Three Of A Kind </span>
                At least three dice the same [Sum of all dice]
            </li>
            <li>
                <span className="fw-bold">Four Of A Kind </span>
                At least four dice the same [Sum of all dice]
            </li>
            <li>
                <span className="fw-bold">Full House </span>
                Three of one number and two of another	[25]
            </li>
            <li>
                <span className="fw-bold">Small Straight </span>
                Four sequential dice [30]
            </li>
            <li>
                <span className="fw-bold">Large Straight </span>
                Five sequential dice [40]
            </li>
            <li>
                <span className="fw-bold">Yahtzee </span>
                All five dice the same [50]
            </li>
             <li>
                <span className="fw-bold">Chance </span>
                Any combination [Sum of all dice]
            </li>
        </ul>
    </Container>);
}