 
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
  computed: {
    width: "3rem",
    height: "3rem",
    padding: "0.5rem",
    border: "1px solid green",
    margin: "0 6px",
    borderRadius: "9px",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    fontWeight: "bold",
    fontSize: "24px",
    background: "gray",
    boxShadow: "rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset",
    transitionDuration:"0.5s",
    cursor: "pointer",
  },
  written: {
     width: "3rem",
    height: "3rem",
    padding: "0.5rem",
    border: "1px solid darkgreen",
    margin: "0 6px",
    borderRadius: "9px",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    fontWeight: "bold",
    fontSize: "24px",
    background: "green",
    boxShadow: "rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset",
    transitionDuration:"0.5s",
  }
}

export default function PointButton(props:any) {
    let {classes} = jss.createStyleSheet(styles).attach()
   return(<>{
    (props.type === "computed") ? 
        <span onClick={() => props.postPoints(props.name, props.point)}className={classes.computed}>{props.point}</span>
        :
        <span className={classes.written}>{props.point}</span>
   }</>)
}