import React, {useState, useEffect} from "react";
import {Col, Row} from "react-bootstrap";
import jss from 'jss'
import PointButton from './PointButton';
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())


let styles = {
  container: {
    background: 'green',
    color: 'white',
    padding: "1rem 0rem",
    height: "100%",
  },
  tableRow: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-around',
    alignItems: 'center',
    margin: "12px 0",
  },
  img: {
    width: "3rem",
    height: "3rem",
    background: "lightgray",
    borderRadius: "8px"
  },
  text: {
    width: "3rem",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    fontWeight: "bold",
  },
  bonus: {
    border: "3px solid yellow",
    borderRadius: "50%",
    width: "4rem",
    height: "4rem",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    color: "yellow"
  },
  bonusActive: {
    borderRadius: "50%",
    width: "4rem",
    height: "4rem",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "yellow",
    color: "black",
    fontWeight: "bolder",
    fontSize: "2rem"
  }
}

export default function TablePoints(props: any) {

    let {classes} = jss.createStyleSheet(styles).attach()
    let [state, setState] = useState({
        equal_1: {
            name: "equal_1: {",
            type: "computed",
            value: 12
        },
        equal_2: {
            name: "equal_2",
            type: "computed",
            value: 11
        },
        equal_3: {
            name: "equal_3",
            type: "computed",
            value: 12
        },
        equal_4: {
            name: "equal_4",
            type: "computed",
            value: 12
        },
        equal_5: {
            name: "equal_5",
            type: "computed",
            value: 12
        },
        equal_6: {
            name: "equal_6",
            type: "computed",
            value: 12
        },
        tris: {
            name: "tris",
            type: "computed",
            value: 14
        },
        poker: {
            name: "poker",
            type: "computed",
            value: 15
        },
        full: {
            name: "full",
            type: "computed",
            value: 16
        },
        little_straight: {
            name: "little_straight",
            type: "computed",
            value: 17
        },
        big_straight: {
            name: "big_straight",
            type: "computed",
            value: 18
        },
        yahtzee: {
            name: "yahtzee",
            type: "computed",
            value: 19
        },
        chance: {
            name: "chance",
            type: "computed",
            value: 20
        }
    })
    useEffect(() => {
        let obj = {...state}
        for (let i in props.points) {
            if (props.points[i] === "" && i !== "yahtzee_bonus") {
                let e:any = props.computedList.find((o:any) => o.name === i)
                let p = 0
                if (e !== undefined && e.points !== 0) {
                    p = e.points
                }
                (obj as any)[i] = {
                    name: i,
                    type: "computed",
                    value: p
                }
            } else {
                (obj as any)[i] = {
                    name: i,
                    type: "written",
                    value: props.points[i]
                }
            }
        }
        setState(obj);
        // FIXME:
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[props.computedList,props.points]);
    return(<Row className={classes.container}>
        <Col xs={6}>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/one.png" alt="one"></img>
                <PointButton name={state.equal_1.name} type={state.equal_1.type} point={state.equal_1.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Ones</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/two.png" alt="two"></img>
                <PointButton name={state.equal_2.name} type={state.equal_2.type} point={state.equal_2.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Twos</span>
            </div>
           <div className={classes.tableRow}>
                <img className={classes.img} src="./img/three.png" alt="three"></img>
                <PointButton name={state.equal_3.name} type={state.equal_3.type} point={state.equal_3.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Threes</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/four.png" alt="four"></img>
                <PointButton name={state.equal_4.name} type={state.equal_4.type} point={state.equal_4.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Fours</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/five.png" alt="five"></img>
                <PointButton name={state.equal_5.name} type={state.equal_5.type} point={state.equal_5.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Fives</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/six.png" alt="six"></img>
                <PointButton name={state.equal_6.name} type={state.equal_6.type} point={state.equal_6.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Sixes</span>
            </div>
           <div className={classes.tableRow}>
                <div className={(props.equalTotal < 63) ? classes.bonus : classes.bonusActive}>
                    {(props.equalTotal < 63) ? `${props.equalTotal} / 63` : "+35"}
                </div>
            </div>
        </Col>
        <Col xs={6}>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/tris.png" alt="tris"></img>
                <PointButton name={state.tris.name} type={state.tris.type} point={state.tris.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Tris</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/poker.png" alt="poker"></img>
                <PointButton name={state.poker.name} type={state.poker.type} point={state.poker.value} postPoints={props.postPoints}/>
                <span className={classes.text}>poker</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/full.png" alt="full"></img>
                <PointButton name={state.full.name} type={state.full.type} point={state.full.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Full House</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/little.png" alt="little"></img>
                <PointButton name={state.little_straight.name} type={state.little_straight.type} point={state.little_straight.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Little Straight</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/large.png" alt="large"></img>
                <PointButton name={state.big_straight.name} type={state.big_straight.type} point={state.big_straight.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Large Straight</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/yatzy.png" alt="yatzy"></img>
                <PointButton name={state.yahtzee.name} type={state.yahtzee.type} point={state.yahtzee.value} postPoints={props.postPoints}/>
                <span className={classes.text}>YATZY</span>
            </div>
            <div className={classes.tableRow}>
                <img className={classes.img} src="./img/chance.png" alt="chance"></img>
                <PointButton name={state.chance.name} type={state.chance.type} point={state.chance.value} postPoints={props.postPoints}/>
                <span className={classes.text}>Chance</span>
            </div>
        </Col>

    </Row>)
}