import {Container,Navbar} from 'react-bootstrap';
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
    navbar: {
        backgroundColor: '#E03737',
        boxShadow: "rgba(0, 0, 0, 0.24) 0px 3px 8px",
        color: "white"
    },
    pointCounter: {
        backgroundColor:"#C01212",
        color: "white",
        fontWeight: "bold",
        fontSize: "1.2rem",
        padding: "0.5rem 1rem",
        borderRadius: "0.5rem",
    }
}
export default function TopBar(props: any) {
    let {classes} = jss.createStyleSheet(styles).attach()
    return (<Navbar className={classes.navbar}>
        <Container>
          <Navbar.Text className={"justify-content-start fw-bold text-white"}>
            Round n {props.round}
          </Navbar.Text>
          <Navbar.Text className={"justify-content-end "+classes.pointCounter}>
            {props.total}
          </Navbar.Text>
        </Container>
      </Navbar>)
}