import React, {useState, useEffect} from 'react';
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
  dicesContainer: {
    width:"100%",
    display: "grid",
    gridTemplateColumns: "repeat(5, 1fr)",
    padding: "1rem 0",
    minHeight: "62px",
  },
  diceButton: {
    border: "2px solid transparent",
    backgroundColor: "white",
    padding: "0.5rem",
  },
  isSide: {
    backgroundColor: "#B0F1B0",
    border: "2px solid #2CB32C"
  }
}

let dice = (value:number) => {
  switch (value) {
    case 1: {
      return <img src='./img/one.png' alt='dice-one' />;
    }
    case 2: {
      return <img src='./img/two.png' alt='dice-two' />;
    }
    case 3: {
      return <img src='./img/three.png' alt='dice-three' />;
    }
    case 4: {
      return <img src='./img/four.png' alt='dice-four' />;
    }
    case 5: {
      return <img src='./img/five.png' alt='dice-five' />;
    }
    case 6: {
      return <img src='./img/six.png' alt='dice-six' />;
    }
  }
}

export default function Dices(props: any) {
    let {classes} = jss.createStyleSheet(styles).attach()
    let [diceState, setDiceState] = useState([false, false, false, false, false])

    useEffect(() => {
      setDiceState([false, false, false, false, false])
    },[props.dices])

    let computeDiceState = (key:number, value: number) => {
      let arr = diceState;
      if (!arr[key]) {
        arr[key] = true;
        props.toSide(value);
      } else {
        arr[key] = false;
        props.toRolled(value);
      }
      setDiceState(arr);
    }



    return (<div className={classes.dicesContainer}>
       {props.dices.map((value:number, key: number) =>
          <button 
            key={key} 
            className={`${classes.diceButton} ${(diceState[key]) ? classes.isSide : ''}`}
            onClick={() => computeDiceState(key, value)}
            disabled={props.turn >= 3}
          >
            {dice(value)}
          </button>
        )}
        </div>)
}

